// tests/layouts/MainLayout.test.tsx
import React from 'react';
import { render } from '@testing-library/react-native';
import { Text } from 'react-native';
import MainLayout from '../../src/layouts/MainLayout';

// useTheme 모킹
jest.mock('../../src/hooks/useTheme', () => ({
  useTheme: () => ({
    theme: {
      colors: {
        background: '#ffffff',
      },
      dark: false,
    },
  }),
}));

// LoadingIndicator 모킹
jest.mock('../../src/components/LoadingIndicator', () => {
  const React = require('react');
  const ReactNative = require('react-native');
  return {
    __esModule: true,
    default: () => React.createElement(ReactNative.View, { testID: 'loading-indicator' }),
  };
});

describe('MainLayout', () => {
  it('renders children correctly', () => {
    const { getByText } = render(
      <MainLayout>
        <Text>메인 콘텐츠</Text>
      </MainLayout>
    );
    
    expect(getByText('메인 콘텐츠')).toBeTruthy();
  });
  
  it('shows loading indicator when loading is true', () => {
    const { getByTestId, queryByText } = render(
      <MainLayout loading={true}>
        <Text>메인 콘텐츠</Text>
      </MainLayout>
    );
    
    expect(getByTestId('loading-indicator')).toBeTruthy();
    expect(queryByText('메인 콘텐츠')).toBeNull();
  });
  
  it('renders header and footer when provided', () => {
    const { getByText } = render(
      <MainLayout
        header={<Text>헤더</Text>}
        footer={<Text>푸터</Text>}
      >
        <Text>메인 콘텐츠</Text>
      </MainLayout>
    );
    
    expect(getByText('헤더')).toBeTruthy();
    expect(getByText('푸터')).toBeTruthy();
    expect(getByText('메인 콘텐츠')).toBeTruthy();
  });
  
  it('applies custom background color when provided', () => {
    const { getByText } = render(
      <MainLayout backgroundColor="#ff0000">
        <Text>메인 콘텐츠</Text>
      </MainLayout>
    );
    
    expect(getByText('메인 콘텐츠')).toBeTruthy();
  });
  
  it('applies custom style when provided', () => {
    const customStyle = { marginTop: 20 };
    const { getByText } = render(
      <MainLayout style={customStyle}>
        <Text>메인 콘텐츠</Text>
      </MainLayout>
    );
    
    expect(getByText('메인 콘텐츠')).toBeTruthy();
  });
  
  it('applies custom contentContainerStyle when provided', () => {
    const customContentContainerStyle = { padding: 20 };
    const { getByText } = render(
      <MainLayout contentContainerStyle={customContentContainerStyle}>
        <Text>메인 콘텐츠</Text>
      </MainLayout>
    );
    
    expect(getByText('메인 콘텐츠')).toBeTruthy();
  });
});